import { defineStore } from "pinia";

import generateRandomPosts from "@/mocks/postsMock";

const getAsyncPosts = async () => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        data: generateRandomPosts(12),
      });
    }, 1000); // Mock 1 second delay
  });
};

export const usePostsStore = defineStore("postsStore", {
  state: () => ({
    posts: null,
    loading: false,
  }),
  getters: {
    getById: (state) => (id) => {
      return state.posts.find((post) => post.id === id);
    },
  },
  actions: {
    async fetchPosts() {
      if (this.posts) return;

      try {
        this.loading = true;
        const response = await getAsyncPosts();
        this.posts = response.data;
      } catch (err) {
        console.error("Failed to load posts", err);
      } finally {
        this.loading = false;
      }
    },
  },
});
