<template>
  <v-text-field
    v-model="searchQuery"
    label="Search"
    prepend-inner-icon="mdi-magnify"
    append-inner-icon="mdi-close"
    @click:append-inner="clearSearch"
    hide-details
    solo
  ></v-text-field>
</template>

<script>
export default {
  name: "SearchField",
  data() {
    return {
      searchQuery: "",
      debouncedQuery: "",
      debounceTimer: null,
    };
  },
  watch: {
    searchQuery(newValue) {
      clearTimeout(this.debounceTimer);

      this.debounceTimer = setTimeout(() => {
        this.debouncedQuery = newValue;
        this.performSearch();
      }, 250);
    },
  },
  methods: {
    clearSearch() {
      this.searchQuery = "";
      this.debouncedQuery = "";
    },
    performSearch() {
      console.log("Searching for:", this.debouncedQuery);
    },
  },
};
</script>
