<template>
  <v-card v-if="post">
    <v-card-title>
      {{ post.title }}
    </v-card-title>
    <v-card-subtitle>
      <router-link :to="'/vendor/' + post.vendorId" class="text-secondary">
        {{ post.vendorName }}
      </router-link>
    </v-card-subtitle>
    <v-card-text>
      <v-img :src="post.image" aspect-ratio="1.8" cover class="mb-2" />
      {{ post.text }}
    </v-card-text>
  </v-card>
</template>

<script setup>
import { computed, defineProps } from "vue";
import { usePostsStore } from "@/stores/postsStore";

const props = defineProps({
  id: String,
});

const post = computed(() => usePostsStore().getById(props.id));
</script>
