import { defineStore } from "pinia";

import {
  saveStateToLocalStorage,
  getStateFromLocalStorage,
} from "./storeUtils.js";

export const useUserStore = defineStore("user", {
  state: () => ({
    favoriteVendors: getStateFromLocalStorage("user-favoriteVendors") || [],
  }),

  getters: {
    isFavoriteVendor: (state) => (vendorId) => {
      return state.favoriteVendors.includes(vendorId);
    },
  },

  actions: {
    toggleFavoriteVendor(vendorId) {
      if (this.isFavoriteVendor(vendorId)) {
        this.favoriteVendors = this.favoriteVendors.filter(
          (id) => id !== vendorId
        );
      } else {
        this.favoriteVendors.push(vendorId);
      }

      saveStateToLocalStorage("user-favoriteVendors", this.favoriteVendors);
    },
  },
});
