<template>
  <GoogleMap
    :api-key="apiKey"
    mapId="appMap"
    class="app-map"
    :center="mapCenter"
    :zoom="15"
  >
    <AdvancedMarker
      v-for="marker in storeMarkers"
      :key="marker.id"
      :options="marker"
      :pinOptions="{ background: 'blue' }"
      @click="showStoreDetails(marker)"
    />
  </GoogleMap>

  <map-details-dialog />
</template>

<script>
import { computed } from "vue";

import storesMock from "@/mocks/storesMock";

import { useMapStore } from "@/stores/map";

import { GoogleMap, AdvancedMarker } from "vue3-google-map";

import MapDetailsDialog from "@/components/map/MapDetailsDialog.vue";

export default {
  name: "MapMain",
  components: { GoogleMap, AdvancedMarker, MapDetailsDialog },
  setup() {
    const mapStore = useMapStore();

    const apiKey = process.env.VUE_APP_GOOGLE_MAPS_API_KEY;

    const mapCenter = {
      lat: 52.209427,
      lng: 21.070871,
    };

    const storeMarkers = computed(() => {
      return storesMock.map((store) => {
        return {
          id: store.id,
          position: store.location,
          title: store.name,
        };
      });
    });

    const showStoreDetails = (vendor) => {
      mapStore.setSelectedItem({
        id: vendor.id,
        type: "vendor",
      });
    };

    return {
      apiKey,
      mapCenter,
      storeMarkers,
      showStoreDetails,
    };
  },
};
</script>

<style>
.app-map {
  height: 100%;
  width: 100%;
}
</style>
