<template>
  <div class="map-details-dialog">
    <v-fade-transition>
      <v-btn
        v-if="dialogComponent"
        icon="mdi-close"
        variant="text"
        class="map-details-dialog__close"
        @click="closeDialog"
      />
    </v-fade-transition>

    <v-slide-x-reverse-transition>
      <component
        v-if="dialogComponent"
        :is="dialogComponent"
        :id="selectedItem.id"
        @close="closeDialog"
      />
    </v-slide-x-reverse-transition>
  </div>
</template>

<script setup>
import { computed } from "vue";
import { useMapStore } from "@/stores/map";

import VendorDetailsDialog from "@/components/vendors/VendorDetailsDialog.vue";
import PostDetailsDialog from "@/components/posts/PostDetailsDialog.vue";
import ProductDetailsDialog from "@/components/products/ProductDetailsDialog.vue";

const mapStore = useMapStore();

const selectedItem = computed(() => mapStore.selectedItem);

const closeDialog = () => mapStore.setSelectedItem(null);

const dialogComponent = computed(() => {
  if (!selectedItem.value) return null;
  const { type } = selectedItem.value;

  const dialogsMap = {
    vendor: VendorDetailsDialog,
    post: PostDetailsDialog,
    product: ProductDetailsDialog,
  };

  return dialogsMap[type] ?? null;
});
</script>

<style lang="scss">
.map-details-dialog {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 1000;
  height: 100%;
  width: 320px;

  .v-card {
    height: calc(100% - 24px);
  }

  .v-card-title {
    padding-right: 34px;
  }
}

.map-details-dialog__close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1001;
}
</style>
