import {
  getRandomImage,
  getRandomElement,
  getRandomDate,
} from "@/mocks/helpers";
import { getRandomStore } from "./storesMock";

const titles = [
  "Fresh Produce Available",
  "New Bakery Items",
  "Local Honey Now Available",
  "Join Our Baking Class",
  "Farm Fresh Dairy",
  "Seasonal Vegetables",
  "Grass-Fed Meat Specials",
  "Organic Snacks Now In Stock",
];

const texts = [
  "We have a fresh batch of organic vegetables ready for purchase.",
  "Our bakery has introduced new pastries, bread, and gluten-free options.",
  "Pick up a jar of our raw honey, perfect for your tea or cooking.",
  "Join us for a weekend baking class and learn how to bake sourdough.",
  "Our dairy products are fresh and come directly from our pasture-raised animals.",
  "Come by and grab some of our seasonal vegetables, freshly picked.",
  "Enjoy our grass-fed beef at a discounted price for a limited time.",
  "Try our new organic snack options, available now at the store.",
];

const generateRandomPost = () => {
  const randomStore = getRandomStore();

  return {
    title: getRandomElement(titles),
    text: getRandomElement(texts),
    date: getRandomDate(),
    vendorName: randomStore.name,
    vendorId: randomStore.id,
    image: getRandomImage(),
    id: Math.floor(Math.random() * 1000), // Random ID
  };
};

const generateRandomPosts = (numPosts) => {
  const posts = [];
  for (let i = 0; i < numPosts; i++) {
    posts.push(generateRandomPost());
  }
  return posts;
};

export default generateRandomPosts;
