<template>
  <div
    v-if="productsStore.loading"
    class="d-flex align-center justify-center pa-6"
  >
    <v-progress-circular indeterminate color="primary" />
  </div>

  <template v-else>
    <template v-if="productsList.length">
      <product-card
        v-for="product in productsList"
        :product="product"
        :key="product.id"
        :hideVendorInfo="!!vendorId"
        @click="openDetailsDialog(product)"
      />
    </template>
    <div v-else>No products found.</div>
  </template>
</template>

<script setup>
import { onMounted, computed, defineProps } from "vue";

import { useProductsStore } from "@/stores/productsStore";
import { useMapStore } from "@/stores/map";

import ProductCard from "@/components/products/ProductCard.vue";

const productsStore = useProductsStore();
const mapStore = useMapStore();

const props = defineProps({
  vendorId: String,
  limit: Number,
});

const openDetailsDialog = ({ id }) => {
  mapStore.setSelectedItem({
    id,
    type: "product",
  });
};

const productsList = computed(() => {
  let filteredProducts = productsStore.products || [];

  if (props.vendorId) {
    filteredProducts = filteredProducts.filter(({ vendorId }) => {
      return vendorId === props.vendorId;
    });
  }

  if (props.limit) {
    filteredProducts = filteredProducts.slice(0, props.limit);
  }

  return filteredProducts;
});

onMounted(() => {
  productsStore.fetchProducts();
});
</script>
