<template>
  <v-card class="content-card">
    <v-tooltip text="Add to favorite">
      <template v-slot:activator="{ props }">
        <v-btn
          v-bind="props"
          :icon="isFavorite ? 'mdi-star' : 'mdi-star-outline'"
          :color="isFavorite ? 'primary' : 'grey'"
          variant="text"
          size="small"
          class="add-to-favorite-button"
          @click.stop="addToFavorite"
        />
      </template>
    </v-tooltip>

    <v-row no-gutters>
      <v-col cols="4">
        <v-img :src="store.image" aspect-ratio="1.0" cover></v-img>
      </v-col>

      <v-col cols="8">
        <v-card-title class="pr-6">{{ store.name }}</v-card-title>
        <v-card-subtitle>{{ store.address }}</v-card-subtitle>
        <v-card-text>
          <div class="truncate-text">
            {{ store.description }}
          </div>
        </v-card-text>
      </v-col>
    </v-row>

    <v-divider></v-divider>

    <v-card-actions>
      <a :href="'mailto:' + store.email" class="secondary text-decoration-none">
        <v-icon>mdi-email</v-icon> {{ store.email }}
      </a>
    </v-card-actions>
  </v-card>
</template>

<script>
import { computed } from "vue";

import { useUserStore } from "@/stores/user";

export default {
  name: "StoreCard",
  props: {
    store: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const userStore = useUserStore();

    const addToFavorite = () => {
      userStore.toggleFavoriteVendor(props.store.id);
    };

    const isFavorite = computed(() => {
      return userStore.isFavoriteVendor(props.store.id);
    });

    return {
      isFavorite,
      addToFavorite,
    };
  },
};
</script>

<style>
.add-to-favorite-button {
  position: absolute;
  top: 4px;
  right: 4px;
}
</style>
