<template>
  <div
    v-if="vendorsStore.loading"
    class="d-flex align-center justify-center pa-6"
  >
    <v-progress-circular indeterminate color="primary" />
  </div>

  <template v-else>
    <vendor-card
      v-for="store in vendorsStore.vendors"
      :store="store"
      :key="store.id"
      @click="openDetailsDialog(store)"
    />
  </template>
</template>

<script setup>
import { onMounted } from "vue";

import { useMapStore } from "@/stores/map";
import { useVendorsStore } from "@/stores/vendorsStore";

import VendorCard from "./VendorCard.vue";

const mapStore = useMapStore();

const vendorsStore = useVendorsStore();

function openDetailsDialog({ id }) {
  mapStore.setSelectedItem({
    id,
    type: "vendor",
  });
}

onMounted(() => {
  vendorsStore.fetchVendors();
});
</script>
